.video {
    width: 100%;
    position: relative;

    &__button {
        display: block;
        width: 10%;
        max-width: 72px;
        border-radius: 50%;
        overflow: hidden;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        background-color: rgba($white, 0.5);
        background-image: url("data:image/svg+xml;base64,");
        background-repeat: no-repeat;
        background-size: 40%;
        background-position: 55% 50%;
        &::before {
            content: '';
            display: block;
            width: 100%;
            padding-bottom: 100%; } } }

video {
    width: 100%;
    max-width: 100%; }
