h1, .h1 {
    font-size: 30px;
    font-weight: 500;
    letter-spacing: 2px;
    line-height: 30px;
    text-transform: uppercase;
    @include r(768) {
        font-size: 24px; } }

h2, .h2 {
    font-size: 30px;
    font-weight: 500;
    letter-spacing: 2px;
    line-height: 30px;
    text-transform: uppercase;
    @include r(768) {
        font-size: 24px; } }

h3, .h3 {
    font-size: 20px;
    font-weight: 500;
    letter-spacing: 0.43px;
    line-height: 20px; }

h4, .h4 {
    font-size: 16px;
    font-weight: 500;
    letter-spacing: 0.44px;
    line-height: 22px; }

p {
    font-size: 16px;
    line-height: 1.15; }
