.modal {
	position: relative;
	left: -0.125em;
	display: inline-block;
	width: 93%;
	max-width: 800px;
	margin: 3rem 2rem;
	vertical-align: middle;
	text-align: left;
	cursor: default;
	background: #FFFFFF;
	box-sizing: border-box;
	white-space: normal;
	@include r(768) {
		width: 95%;
		margin: 3rem 2%; }

	&:not(.is-visible) {
		opacity: 0;
		transform: scale(0.9);
		transition: transform 0.3s, opacity 0.3s; }

	&.is-visible {
		opacity: 1;
		transition: transform 0.3s 0.2s, opacity 0.3s 0.2s; }

	&:not(.is-hidden) {
		visibility: visible;
		overflow-y: visible; }

	&.is-hidden {
		position: absolute;
		top: 0;
		left: 0;
		height: 0;
		visibility: hidden; } }

.modal-close-btn {
	position: absolute;
	top: -5px;
	right: -35px;
	font-size: 30px;
	line-height: 30px;
	width: 30px;
	height: 30px;
	color: white;
	transition: transform 0.15s ease-in-out;
	transform: rotate(90deg);

	@include notouch {
		&:hover {
			transform: rotate(0); } }

	@include r(1024) {
		top: -35px;
		right: -5px; } }



.modal-content {
	padding: 60px 60px 70px 80px;
	@include r(1024) {
		padding: 50px 40px 60px 40px; }
	@include r(768) {
		padding: 35px 25px 45px 25px; }
	&__title {
		font-size: 30px;
		margin-bottom: 40px;
		color: $main;
		font-weight: 500;
		font-family: $f1;
		letter-spacing: 2px;
		line-height: 1.1;
		text-transform: uppercase;
		@include r(768) {
			margin-bottom: 25px; } }
	.content {
		max-width: 380px;
		ul:not([class]) {
			margin-top: 10px;
			margin-bottom: 10px; }
		h3 {
			margin-top: 29px; }
		& > *:first-child {
			margin-top: 0; }
		ul:not([class]),
		ol:not([class]) {
			li + li {
				margin-top: 15px; } } } }


.modal-info {
	display: flex;
	flex-flow: row wrap;
	&__text {
		width: 48%;
		padding-right: 20px;
		@include r(768) {
			width: 100%;
			order: 1; } }
	&__img {
		width: 52%;
		@include r(768) {
			width: 100%;
			order: 0;
			margin-bottom: 30px; } } }

.modal-info-img {
	width: 100%;
	max-width: 520px;
	position: relative;
	font-size: 0;
	@include rm(769) {
		margin-top: 10px; }
	&::before {
		content: '';
		display: block;
		width: 100%;
		padding-bottom: 81.75%; }
	img {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		max-width: 100%;
		max-height: 100%;
		object-fit: cover; } }
.modal-footer {
	display: flex;
	flex-flow: row wrap;
	align-items: center;
	&__item {
		width: 25%;
		padding: 33px 25px;
		background: $main;
		text-align: center;
		position: relative;
		flex-grow: 1;
		&:not(:last-child) {
			&::after {
				content: '';
				display: block;
				width: 1px;
				height: 40px;
				background: $white;
				position: absolute;
				@include rm(769) {
					right: 0;
					top: 50%;
					transform: translateY(-50%); }
				@include r(768) {
					left: 0;
					bottom: 0;
					width: 100%;
					height: 1px;
					opacity: .5; } } }
		@include r(768) {
			width: 100%;
			padding: 25px; } }
	&__link {
		display: inline-block;
		color: $white;
		font-size: 20px;
		font-weight: 500;
		letter-spacing: 1px;
		line-height: 1.1;
		text-decoration: underline;
		letter-spacing: 0.43px;
		&:hover {
			text-decoration: none; } } }
