.long-card {
    width: 100%;
    font-size: 0;
    display: flex;
    flex-flow: row wrap;
    overflow: hidden;
    @include r(768) {
        flex-flow: column-reverse wrap; }
    &-wrap {
        // padding: 70px 0
        // +r(1024)
        //     padding: 60px 0 80px 0
        &:not(:only-child) {
            margin-top: 70px;
            &:not(:last-child) {
                margin-bottom: 70px;
                @include r(1024) {
                    margin-bottom: 50px; } }
            @include r(1024) {
                margin-top: 50px; } }
        &--border {
            border-top: 1px solid rgba(#231148,0.07); }
        &--inside {
            border-top: 1px solid rgba(#231148,0.07);
            padding: 0;
            padding-top: 80px;
            margin-top: 80px;
            @include r(1024) {
                padding: 0;
                padding-top: 60px;
                margin-top: 60px; }
            .long-card {
                &__info {
                    @include r(768) {
                        padding-left: 0;
                        padding-right: 0; } } } }
        .long-card {
            &:nth-child(even) {
                @include rm(769) {
                    flex-flow: row-reverse wrap;
                    .long-card__info {
                        padding-left: 25px;
                        align-items: flex-start;
                        &::before {
                            left: auto;
                            right: 100%;
                            margin-left: auto;
                            margin-right: -1px;
                            transform: scaleX(-1); } } } } } }
    & + .long-card {
        margin-top: 50px;
        @include r(1024) {
            margin-top: 30px; } }
    &__image {
        display: block;
        position: relative;
        width: 100%;
        @include rm(769) {
            width: 55.5%; }
        &::after {
            content: '';
            display: block;
            width: 100%;
            height: 0;
            padding-bottom: 81%; }
        img {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-fit: cover;
            &.long-card-image {
                display: none;
                @include rm(769) {
                    display: block; } }
            &.long-card-image-mobile {
                display: block;
                @include rm(769) {
                    display: none; } } } }
    &__info {
        display: flex;
        flex-flow: column nowrap;
        justify-content: center;
        padding: 45px 25px;
        position: relative;
        z-index: 1;
        width: 100%;
        background-color: $main;
        @include rm(769) {
            padding-left: 60px; }
        &::before {
            content: '';
            display: block;
            height: 100%;
            position: absolute;
            left: 100%;
            margin-left: -1px;
            top: 0;
            width: 88px;
            background-repeat: no-repeat;
            background-size: cover;
            background-position: center center;
            background-image: url('data:image/png;base64,');
            @include r(768) {
                left: 0;
                top: auto;
                bottom: 100%;
                width: 100%;
                height: 40%;
                background-position: bottom;
                background-size: contain;
                background-image: url('data:image/png;base64,'); } }
        @include r(768) {
            padding: 30px 30px 45px 30px; }
        @include rm(769) {
            width: 44.5%;
            align-items: flex-end; }
        &-inner {
            text-align: left;
            max-width: 425px; } }
    &__logo {
        margin-bottom: 15px;
        img {} }
    &__title {
        margin-bottom: 20px;
        font-size: 30px;
        font-weight: 500;
        letter-spacing: 2px;
        line-height: 1.1;
        text-transform: uppercase;
        @include r(768) {
            font-size: 25px; } }
    p {
        font-size: 16px;
        font-weight: 400;
        letter-spacing: 0.38px;
        line-height: 26px; }
    p + p {
        margin-top: 5px; }
    .bttn {
        margin-top: 35px; } }
