.product-additionally {
    display: flex;
    flex-flow: row wrap;
    &__aside {
        margin-bottom: 15px;
        width: 100%;
        &:first-child {
            padding-right: 20px; }
        @include rm(1025) {
            width: 32%; } }
    &__content {
        width: 100%;
        // padding-left: calc((100vw - #{$max-content-width}) / 2 - #{$scroll-width} / 2)
        @include rm(1025) {
            width: 68%; } } }
