.header-find {
    flex-grow: 1;
    &__button {
        padding: 11px 20px;
        width: 100%;
        letter-spacing: 1.19px;
        font-size: 19px;
        font-weight: 400;
        line-height: 1;
        @include r(1024) {
            display: none; } }
    &__pin {
        display: block;
        width: 30px;
        height: 30px;
        color: $main;
        @include rm(1025) {
            display: none; } } }
