.input-row {
    display: flex;
    flex-flow: row wrap;
    width: 100%;
    @include rm(651) {
        width: calc(100% + 16px);
        margin-left: -8px;
        margin-right: -8px;
 }        // margin-bottom: 20px
    &:last-child {
        margin-bottom: 0; }
    &__label {
        width: 100%;
        margin-bottom: 20px;
        margin-top: 20px;
        @include rm(651) {
            margin-left: 8px;
            margin-right: 8px; } }
    &__link {
        width: 100%;
        a {
            color: $main;
            font-size: 16px;
            font-weight: 400;
            text-decoration: underline;
            letter-spacing: 0.34px;
            &:hover {
                text-decoration: none; } }
        @include rm(651) {
            margin-left: 8px;
            margin-right: 8px; } } }

.input-group {
    display: block;
    @include rm(651) {
        width: calc(100% + 16px);
        margin-left: -8px;
        margin-right: -8px; } }
