.accordion {
	$this: &;
	display: block;
	text-align: left;
	&--product {
		#{$this}__quest {
			font-size: 20px;
			line-height: 1;
			padding-top: 17px;
			padding-bottom: 10px; }
		.accordion-item {
			border-top: 1px solid $main;
			&:last-child {
				border-bottom: 1px solid $main; } } } }
.accordion-item {
	position: relative;
	padding: 7px 0 17px 0;
	border-top: 1px solid rgba($main, .05);
	// &:last-child
 }	// 	border-bottom: 1px solid rgba($main, .05)


.accordion__quest {
	width: 100%;
	display: block;
	padding-top: 10px;
	cursor: pointer;
	user-select: none;
	color: $main;
	transition: color .3s;
	font-size: 16px;
	line-height: calc(20/16);
	font-weight: 600;
	letter-spacing: 1px;
	position: relative;
	text-decoration: none;
	padding-right: 35px;
	@include r(1024) {
		font-size: 16px; }
	&:hover {
		color: rgba($main, .8); }
	&-icon {
		display: block;
		width: 14px;
		height: 8px;
		font-size: 0;
		position: absolute;
		right: 6px;
		top: 17px;
		transform: rotate(0);
		transition: transform .3s;
		background-image: url("data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHdpZHRoPSIxMyIgaGVpZ2h0PSI3Ij48cGF0aCBmaWxsPSIjMzA4YmExIiBkPSJNMTEuMDQ1LjY2YS41MDcuNTA3IDAgMDEuNzA0LjcyOUw2LjM0NiA2LjYwOGEuNTA2LjUwNiAwIDAxLS43MDQgMEwuMjQ0IDEuMzg4QS41MDYuNTA2IDAgMTEuOTQ4LjY2bDUuMDQ2IDQuODh6Ii8+PC9zdmc+");
		background-repeat: no-repeat; }
	&.active {
		.accordion__quest-icon {
			transform: rotate(180deg); } } }

.accordion__answer {
	display: none;
	padding: 15px 50px 5px 20px;
	opacity: 0;
	transition: opacity .2s 0s;
	@include r(768) {
		padding-left: 0; }
	p + p {
		margin-top: 5px; }
	&.active {
		opacity: 1;
		transition: opacity .2s .2s; } }

.accordion-wrap {
	display: block;
	&:not(:only-child) {
		margin-top: 45px;
		@include r(768) {
			margin-top: 30px; } }
	&--product {
		@include r(768) {
			margin-top: 30px; } } }
.accordion-title {
	margin: 0;
	margin-bottom: 35px; }


.accordion-devider {
	border: none;
	width: 100%;
	height: 1px;
	padding: 0;
	margin: 0;
	background: $main;
	opacity: 0.5;
	margin-bottom: 20px; }
