.product-icons {
    display: flex;
    justify-content: space-between;
    flex-flow: row wrap;
    width: 100%;
    background: $main;
    position: relative;
    z-index: 1;
    @include rm(1025) {
        flex-flow: column nowrap;
        justify-content: flex-start;
        align-items: flex-end;
        margin-left: auto;
        margin-right: 0;
        max-width: 140px;
        min-height: 100%; }
    @include r(1024) {
        margin-top: 120px; }
    &::before {
        content: '';
        display: block;
        position: absolute;
        background-repeat: no-repeat;
        @include rm(1025) {
            height: calc(100% + 80px + 70px);
            width: 100px;
            top: -80px;
            right: 130%;
            z-index: -1;
            background-position: right center;
            background-size: 100px 100%;
            background-image: url('data:image/png;base64,'); }
        @include r(1024) {
            width: 100vw;
            left: -20px;
            bottom: 100%;
            height: 60px;
            background-size: 100% 60px;
            background-image: url('data:image/png;base64,'); } }
    &::after {
        content: '';
        display: block;
        background: $main;
        z-index: -1;
        position: absolute;
        @include rm(1025) {
            height: calc(100% + 80px + 70px);
            width: 40vw;
            top: -80px;
            left: -31%; }
        @include r(1024) {
            width: 100vw;
            height: calc( 100% + 40px);
            left: -20px;
            top: 0; } }
    &__item {
        @include rm(1025) {
            &:not(:last-child) {
                margin-bottom: 40px; } }
        font-size: 0;
        @include r(1024) {
            width: 50%;
            text-align: center;
            padding: 20px; } }
    &__img {
        @include r(768) {
            max-width: 80%; } } }
