.footer {
    background: $main;
    padding: 30px 40px;
    position: relative;
    z-index: 1;
    overflow: hidden;
    @include r(1024) {
        padding: 30px 20px; }
    // &-inner
    //     width: 100%
    //     max-width: 1280px
    //     margin: 0 auto
    &-row {
        display: flex;
        align-items: flex-end;
        @include r(1024) {
            flex-flow: column nowrap;
            align-items: flex-start; } }
    &__current {
        margin-bottom: 5px;
        @include r(1024) {
            margin-bottom: 25px; } }
    // &__logo
    //     width: 157px
    //     margin: 0 0 16px auto
    //     +r(1024)
    //         margin: 0 auto 40px 0
    &__nav {
        width: 80%;
        max-width: 1100px;
        margin-right: auto;
        padding-right: 10px;
        @include r(1024) {
            width: 100%;
            margin: 0 auto 40px 0; } }
    &__rights {
        margin-left: 20px;
        text-align: right;
        &::before {
            content: '';
            display: block;
            position: absolute;
            right: 0;
            bottom: 0;
            height: 100%;
            width: 100%;
            max-width: 304px;
            max-height: 136px;
            background-size: cover;
            z-index: -1;
            background-repeat: no-repeat;
            background-image: url('data:image/png;base64,');
            @include r(1024) {
                max-width: 383px;
                width: 110%;
                right: -40px; } }
        @include r(1024) {
            margin-left: auto; } } }
